<template>
  <div v-resize="onResize">
    <v-progress-linear v-if="loading" indeterminate color="blue" top />
    <div v-else class="media-wrapper" @mousemove="onMouseMove">
      <Keypress key-event="keyup" @success="keyPressed" />
      <div class="media-nav">
        <v-row>
          <v-col class="ma-4">
            <v-icon large color="grey lighten-2" @click="handlePrevious">
              mdi-chevron-left-circle
            </v-icon>
          </v-col>
          <v-spacer></v-spacer>
          <v-col justify="end" align="right" class="ma-3">
            <v-icon large color="grey lighten-2" @click="handleNext">
              mdi-chevron-right-circle
            </v-icon>
          </v-col>
        </v-row>
      </div>
      <div class="head">
        <v-row>
          <v-col class="ml-2">
            <v-icon @click="handleHome" color="white"> mdi-home </v-icon>
            {{ media.dateTaken | dateformat("DATETIME_MED") }}
          </v-col>
          <v-col class="mr-4" align="right">
            <v-icon
              :color="media.isFavorite ? 'red' : 'white'"
              class="mr-4"
              @click="toggleFavorite(media)"
            >
              mdi-heart
            </v-icon>

            <v-icon color="white"> mdi-cog-outline </v-icon>
          </v-col>
        </v-row>
      </div>
      <div class="foot">
        {{ geoLocation }}
      </div>
      <img :src="imageSrc" @load="onImgLoaded" ref="img" />
      <div v-show="image.loaded">
        <template v-for="face in media.faces">
          <FaceBox :key="face.id" :face="face" :image="image"></FaceBox>
        </template>
      </div>
    </div>
    <div class="filmstripe" v-show="showStripe">
      <FilmStripe></FilmStripe>
    </div>
  </div>
</template>

<script>
import FaceBox from "./FaceBox";
import FilmStripe from "./FilmStripe.vue";
//import debounce from "lodash";

export default {
  data() {
    return {
      image: {
        loaded: false,
        width: 0,
        naturalWidth: 0,
        offsetLeft: 0,
        offsetTop: 0,
      },
      showStripe: false,
      keyboardKeys: [
        {
          keyCode: 37, // Left
          preventDefault: false,
        },
        {
          keyCode: 39, // Right
          preventDefault: false,
        },
        {
          keyCode: 27, // ESC
          preventDefault: false,
        },
      ],
      mediaId: this.$route.params.id,
      windowWidth: window.innerWidth,
    };
  },
  components: { FaceBox, Keypress: () => import("vue-keypress"), FilmStripe },
  created() {
    //this.onResize = debounce(this.onResize, 1000);
    //this.onMouseMove = debounce(this.onMouseMove, 500);
  },
  computed: {
    thumbnail: function () {
      if (this.$store) {
        const existing = this.$store.state.media.list.filter(
          (x) => x.id === this.$route.params.id
        );

        if (existing.length > 0) {
          return existing[0].thumbnail.dataUrl;
        }
      }

      return null;
    },

    imageSrc: function () {
      return "/api/media/webimage/" + this.media.id;
    },
    loading: function () {
      return this.media === null;
    },
    media: function () {
      return this.$store.state.media.current;
    },
    geoLocation: function () {
      if (this.media.geoLocation && this.media.geoLocation.address) {
        return this.media.geoLocation.address.name;
      }
      return null;
    },
    box: function () {
      const media = this.media;

      if (media) {
        const w = document.querySelector("#app").clientWidth;
        const h = document.querySelector("#app").clientHeight;
        const screenOrientation = w > h ? "l" : "p";

        const box = { left: 0, top: 0 };

        if (screenOrientation === "l") {
          const ar = media.dimension.height / h;
          box.height = h;
          box.width = media.dimension.width / ar;
          box.left = (w - box.width) / 2;
        } else {
          const ar = media.dimension.width / w;
          box.width = w;
          box.height = media.dimension.height / ar;
          box.top = (h - box.height) / 2;
        }
        return box;
      }

      return null;
    },
  },
  methods: {
    onImgLoaded() {
      this.$nextTick(() => {
        window.setTimeout(() => {
          this.setImage();
        }, 500);
      });
    },
    setImage() {
      if (this.$refs.img && this.$refs.img.width) {
        this.image = {
          width: this.$refs.img.width,
          naturalWidth: this.$refs.img.naturalWidth,
          offsetLeft: this.$refs.img.offsetLeft - this.$refs.img.width / 2,
          offsetTop: this.$refs.img.offsetTop - this.$refs.img.height / 2,
          loaded: true,
        };
      }
    },
    handlePrevious: function () {
      this.navigate(-1);
    },
    handleNext: function () {
      this.navigate(+1);
    },
    navigate: function (step) {
      this.image.loaded = false;
      var nextId = this.$store.getters["next"](step);
      if (nextId) {
        this.$store.dispatch("media/show", nextId);
      } else {
        this.handleHome();
      }
    },
    handleHome: function () {
      this.$store.dispatch("media/close");
    },
    onMouseMove: function (e) {
      this.showStripe = e.clientY > 300;
    },
    toggleFavorite: function (media) {
      console.log(media);
      this.$store.dispatch("media/toggleFavorite", media);
    },
    keyPressed: function (e) {
      switch (e.event.keyCode) {
        case 37:
          this.navigate(-1);
          break;
        case 39:
          this.navigate(1);
          break;
        case 27:
          this.handleHome();
          break;
      }
    },
    onResize() {
      this.setImage();
    },
  },
};
</script>

<style scoped>
.media-wrapper {
  background-color: #1b1b1c;
  height: 100vh;
}
.media-wrapper img {
  height: 100vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.media-nav {
  position: absolute;
  display: flex;
  width: 99%;
  top: 44vh;
  z-index: 100;
}

.head {
  color: #fff;
  line-height: 20px;
  position: absolute;
  padding: 0;
  width: 100%;
  left: 0;
  top: 0;
  background-color: #000;
  opacity: 0.5;
  z-index: 10;
  height: 48px;
}

.foot {
  color: #fff;
  position: absolute;
  bottom: 0;
  padding: 4px;
  z-index: 10;
  background-color: #000;
  opacity: 0.5;
  width: 100%;
}

.filmstripe {
  position: absolute;
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
  bottom: 50px;
  width: 100%;
  z-index: 10;
}
</style>

